import React, { useState } from "react";
import styled from "styled-components";

import {
  ABSTRACT_OFFSET,
  CONCEPT_OFFSET,
  RESEARCH_OFFSET,
  RESEARCH_34_OFFSET,
  EXPERIMENT_OFFSET,
  EXPERIMENT_2_OFFSET,
  EXPERIMENT_3_OFFSET,
  EXPERIMENT_4_OFFSET,
  CONCLUSION_OFFSET,
  BIBLIOGRAPHY_OFFSET,
  ABSTRACT,
  CONCEPT,
  CONCEPT_BACKGROUND,
  CONCEPT_IDEA,
  RESEARCH,
  RESEARCH_UNIVERSAL,
  RESEARCH_METHOD,
  RESEARCH_COLOR,
  RESEARCH_FEATURES,
  EXPERIMENT,
  EXPERIMENT_GRAPHIC,
  EXPERIMENT_COLOR,
  EXPERIMENT_MOVEMENT,
  EXPERIMENT_PROTOTYPE,
  EXPERIMENT_PROTOTYPE_NEWS,
  EXPERIMENT_PROTOTYPE_WEATHER,
  EXPERIMENT_PROTOTYPE_MATERIAL,
  CONCLUSION,
  BIBLIOGRAPHY,
  linkSection,
} from "../../utils";

const Index = styled.div`
  padding-bottom: 12px;
`;
const IndexTitle = styled.div`
  height: 24px;
  background-color: #fff;
  display: flex;
  align-items: center;
  padding-left: 472px;
`;
const IndexTitleText = styled.div`
  color: #000;
  font-size: 14px;
  letter-spacing: 0.5px;
  line-height: 18px;
`;
const IndexBody = styled.div`
  padding-left: 472px;
`;
const IndexGroup = styled.div`
  border-bottom: 1px solid #fff;
  ${({ noBorder }) => noBorder && `border-bottom: none;`}
`;
const IndexRow = styled.div`
  display: flex;
  flex-direction: row;
`;
const IndexCol = styled.div`
  ${({ flex }) => flex && `flex: 1;`}
  ${({ first }) => first && `width: 232px;`}
`;

const IndexText = styled.div`
  font-size: 18px;
  height: 23px;
  display: flex;
  align-items: center;
  flex-direction: row;
`;

const IndexArrow = styled.div`
  background-image: url(/assets/images/arrow.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 2px;
  right: 8px;
  cursor: pointer;
`;

const IndexTextWrap = styled.div`
  ${({ border }) => border && `border-bottom: 1px solid #fff;`}
  position: relative;
  cursor: pointer;

  ${({ active }) =>
    active &&
    `
    ${IndexText} {
      color: #75ff6f
    }

    ${IndexArrow} {
      background-image: url(/assets/images/arrow_gr.png);
    }
  `}
`;

function IndexComponent() {
  const [depthFirst, setDepthFirst] = useState("");
  const [depthSecond, setDepthSecond] = useState("");
  const [depthThird, setDepthThird] = useState("");

  const hoverIndexText = (text) => {
    switch (text) {
      case ABSTRACT:
        setDepthFirst(ABSTRACT);
        return;
      case CONCEPT_BACKGROUND:
        setDepthFirst(CONCEPT);
        setDepthSecond(CONCEPT_BACKGROUND);
        return;
      case CONCEPT_IDEA:
        setDepthFirst(CONCEPT);
        setDepthSecond(CONCEPT_IDEA);
        return;
      case RESEARCH_UNIVERSAL:
        setDepthFirst(RESEARCH);
        setDepthSecond(RESEARCH_UNIVERSAL);
        return;
      case RESEARCH_METHOD:
        setDepthFirst(RESEARCH);
        setDepthSecond(RESEARCH_METHOD);
        return;
      case RESEARCH_COLOR:
        setDepthFirst(RESEARCH);
        setDepthSecond(RESEARCH_COLOR);
        return;
      case RESEARCH_FEATURES:
        setDepthFirst(RESEARCH);
        setDepthSecond(RESEARCH_FEATURES);
        return;
      case EXPERIMENT_GRAPHIC:
        setDepthFirst(EXPERIMENT);
        setDepthSecond(EXPERIMENT_GRAPHIC);
        return;
      case EXPERIMENT_COLOR:
        setDepthFirst(EXPERIMENT);
        setDepthSecond(EXPERIMENT_COLOR);
        return;
      case EXPERIMENT_MOVEMENT:
        setDepthFirst(EXPERIMENT);
        setDepthSecond(EXPERIMENT_MOVEMENT);
        return;
      case EXPERIMENT_PROTOTYPE_NEWS:
        setDepthFirst(EXPERIMENT);
        setDepthSecond(EXPERIMENT_PROTOTYPE);
        setDepthThird(EXPERIMENT_PROTOTYPE_NEWS);
        return;
      case EXPERIMENT_PROTOTYPE_WEATHER:
        setDepthFirst(EXPERIMENT);
        setDepthSecond(EXPERIMENT_PROTOTYPE);
        setDepthThird(EXPERIMENT_PROTOTYPE_WEATHER);
        return;
      case EXPERIMENT_PROTOTYPE_MATERIAL:
        setDepthFirst(EXPERIMENT);
        setDepthSecond(EXPERIMENT_PROTOTYPE);
        setDepthThird(EXPERIMENT_PROTOTYPE_MATERIAL);
        return;
      case CONCLUSION:
        setDepthFirst(CONCLUSION);
        return;
      case BIBLIOGRAPHY:
        setDepthFirst(BIBLIOGRAPHY);
        return;
      default:
        setDepthFirst("");
        setDepthSecond("");
        setDepthThird("");
        return;
    }
  };

  return (
    <Index>
      <IndexTitle>
        <IndexTitleText>TABLE OF CONTENTS</IndexTitleText>
      </IndexTitle>
      <IndexBody>
        <IndexGroup>
          <IndexRow>
            <IndexCol flex>
              <IndexTextWrap
                flex
                active={depthFirst === ABSTRACT}
                onMouseEnter={() => hoverIndexText(ABSTRACT)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(ABSTRACT_OFFSET)}
              >
                <IndexText>Abstract</IndexText>
                <IndexArrow />
              </IndexTextWrap>
            </IndexCol>
          </IndexRow>
        </IndexGroup>
        <IndexGroup>
          <IndexRow>
            <IndexCol first>
              <IndexTextWrap
                active={depthFirst === CONCEPT}
                onMouseEnter={() => hoverIndexText(CONCEPT_BACKGROUND)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(CONCEPT_OFFSET)}
              >
                <IndexText>Concept</IndexText>
              </IndexTextWrap>
            </IndexCol>
            <IndexCol flex>
              <IndexTextWrap
                border
                active={depthSecond === CONCEPT_BACKGROUND}
                onMouseEnter={() => hoverIndexText(CONCEPT_BACKGROUND)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(CONCEPT_OFFSET)}
              >
                <IndexText>Background</IndexText>
                <IndexArrow />
              </IndexTextWrap>
              <IndexTextWrap
                active={depthSecond === CONCEPT_IDEA}
                onMouseEnter={() => hoverIndexText(CONCEPT_IDEA)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(CONCEPT_OFFSET)}
              >
                <IndexText>Idea</IndexText>
                <IndexArrow />
              </IndexTextWrap>
            </IndexCol>
          </IndexRow>
        </IndexGroup>
        <IndexGroup>
          <IndexRow>
            <IndexCol first>
              <IndexTextWrap
                active={depthFirst === RESEARCH}
                onMouseEnter={() => hoverIndexText(RESEARCH_UNIVERSAL)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(RESEARCH_OFFSET)}
              >
                <IndexText>Research</IndexText>
              </IndexTextWrap>
            </IndexCol>
            <IndexCol flex>
              <IndexTextWrap
                border
                active={depthSecond === RESEARCH_UNIVERSAL}
                onMouseEnter={() => hoverIndexText(RESEARCH_UNIVERSAL)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(RESEARCH_OFFSET)}
              >
                <IndexText>
                  Universal Facial Expressions of Human Emotion
                </IndexText>
                <IndexArrow />
              </IndexTextWrap>
              <IndexTextWrap
                border
                active={depthSecond === RESEARCH_METHOD}
                onMouseEnter={() => hoverIndexText(RESEARCH_METHOD)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(RESEARCH_OFFSET)}
              >
                <IndexText>Method to Express an AI's Emotions</IndexText>
                <IndexArrow />
              </IndexTextWrap>
              <IndexTextWrap
                border
                active={depthSecond === RESEARCH_COLOR}
                onMouseEnter={() => hoverIndexText(RESEARCH_COLOR)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(RESEARCH_34_OFFSET)}
              >
                <IndexText>Color Theory</IndexText>
                <IndexArrow />
              </IndexTextWrap>
              <IndexTextWrap
                active={depthSecond === RESEARCH_FEATURES}
                onMouseEnter={() => hoverIndexText(RESEARCH_FEATURES)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(RESEARCH_34_OFFSET)}
              >
                <IndexText>Features of Human Facial Expressions</IndexText>
                <IndexArrow />
              </IndexTextWrap>
            </IndexCol>
          </IndexRow>
        </IndexGroup>
        <IndexGroup>
          <IndexRow>
            <IndexCol first>
              <IndexTextWrap
                active={depthFirst === EXPERIMENT}
                onMouseEnter={() => hoverIndexText(EXPERIMENT_GRAPHIC)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(EXPERIMENT_OFFSET)}
              >
                <IndexText>Experiment</IndexText>
              </IndexTextWrap>
            </IndexCol>
            <IndexCol flex>
              <IndexTextWrap
                border
                active={depthSecond === EXPERIMENT_GRAPHIC}
                onMouseEnter={() => hoverIndexText(EXPERIMENT_GRAPHIC)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(EXPERIMENT_OFFSET)}
              >
                <IndexText>Grahic Shape</IndexText>
                <IndexArrow />
              </IndexTextWrap>
              <IndexTextWrap
                border
                active={depthSecond === EXPERIMENT_COLOR}
                onMouseEnter={() => hoverIndexText(EXPERIMENT_COLOR)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(EXPERIMENT_2_OFFSET)}
              >
                <IndexText>Color system</IndexText>
                <IndexArrow />
              </IndexTextWrap>
              <IndexTextWrap
                border
                active={depthSecond === EXPERIMENT_MOVEMENT}
                onMouseEnter={() => hoverIndexText(EXPERIMENT_MOVEMENT)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(EXPERIMENT_3_OFFSET)}
              >
                <IndexText>Movement</IndexText>
                <IndexArrow />
              </IndexTextWrap>
              <IndexRow>
                <IndexCol first>
                  <IndexTextWrap
                    active={depthSecond === EXPERIMENT_PROTOTYPE}
                    onMouseEnter={() =>
                      hoverIndexText(EXPERIMENT_PROTOTYPE_NEWS)
                    }
                    onMouseLeave={() => hoverIndexText("")}
                    onClick={() => linkSection(EXPERIMENT_4_OFFSET)}
                  >
                    <IndexText>Prototype</IndexText>
                  </IndexTextWrap>
                </IndexCol>
                <IndexCol flex>
                  <IndexTextWrap
                    border
                    active={depthThird === EXPERIMENT_PROTOTYPE_NEWS}
                    onMouseEnter={() =>
                      hoverIndexText(EXPERIMENT_PROTOTYPE_NEWS)
                    }
                    onMouseLeave={() => hoverIndexText("")}
                    onClick={() => linkSection(EXPERIMENT_4_OFFSET)}
                  >
                    <IndexText>News Information</IndexText>
                    <IndexArrow />
                  </IndexTextWrap>
                  <IndexTextWrap
                    border
                    active={depthThird === EXPERIMENT_PROTOTYPE_WEATHER}
                    onMouseEnter={() =>
                      hoverIndexText(EXPERIMENT_PROTOTYPE_WEATHER)
                    }
                    onMouseLeave={() => hoverIndexText("")}
                    onClick={() => linkSection(EXPERIMENT_4_OFFSET)}
                  >
                    <IndexText>Weather Information</IndexText>
                    <IndexArrow />
                  </IndexTextWrap>
                  <IndexTextWrap
                    active={depthThird === EXPERIMENT_PROTOTYPE_MATERIAL}
                    onMouseEnter={() =>
                      hoverIndexText(EXPERIMENT_PROTOTYPE_MATERIAL)
                    }
                    onMouseLeave={() => hoverIndexText("")}
                    onClick={() => linkSection(EXPERIMENT_4_OFFSET)}
                  >
                    <IndexText>Material Effects</IndexText>
                    <IndexArrow />
                  </IndexTextWrap>
                </IndexCol>
              </IndexRow>
            </IndexCol>
          </IndexRow>
        </IndexGroup>
        <IndexGroup>
          <IndexRow>
            <IndexCol flex>
              <IndexTextWrap
                active={depthFirst === CONCLUSION}
                onMouseEnter={() => hoverIndexText(CONCLUSION)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(CONCLUSION_OFFSET)}
              >
                <IndexText>Conclusion</IndexText>
                <IndexArrow />
              </IndexTextWrap>
            </IndexCol>
          </IndexRow>
        </IndexGroup>
        <IndexGroup noBorder>
          <IndexRow>
            <IndexCol flex>
              <IndexTextWrap
                active={depthFirst === BIBLIOGRAPHY}
                onMouseEnter={() => hoverIndexText(BIBLIOGRAPHY)}
                onMouseLeave={() => hoverIndexText("")}
                onClick={() => linkSection(BIBLIOGRAPHY_OFFSET)}
              >
                <IndexText>Bibliography</IndexText>
                <IndexArrow />
              </IndexTextWrap>
            </IndexCol>
          </IndexRow>
        </IndexGroup>
      </IndexBody>
    </Index>
  );
}

export default IndexComponent;
